/* Sidebar styles */
.sidebar {
  width: 200px;
  height: 100vh;
  position: fixed;
  top: 60px; /* Adjust for Navbar height */
  left: -200px; /* Initially hidden */
  background-color: #dbe8c8;
  padding-top: 20px;
  transition: left 0.3s ease-in-out;
  z-index: 1000;
}

.sidebar.open {
  left: 0;
  background-color:#dbe8c8;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  padding: 15px 20px;
}

.sidebar ul li a {
  color: white;
  text-decoration: none;
}

.sidebar ul li a:hover {
  background-color: #4dc728;
  padding: 10px 10px;
  border-radius: 1rem;

  
}

/* Sidebar toggle button */
.sidebar-toggle {
  position: fixed;
  top: 70px;
  left: 20px;
  background-color: #555f45;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 1001;
  font-size: 16px;
}

.sidebar-toggle.open {
  left: 210px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sidebar {
      width: 50%;
      max-width: 200px;
  }
  .sidebar-toggle {
      top: 70px;
      left: 10px;
  }
}

@media (max-width: 480px) {
  .sidebar {
      width: 60%;
  }
  .sidebar-toggle {
      left: 5px;
  }
}
